@import '../../styles/variables.scss';

.movie-page {
  overflow: hidden;
  
  &__grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    
    .right {
      display: flex;
      flex-direction: column;
      align-items: end;
    }
    
    &_long {
      grid-column: 1 / 3;
    }
  }
}

@media (max-width: 767.98px) {
  .movie-page {

    &__grid {
      grid-template-columns: 1fr;
      gap: 0;

      .right {
        align-items: start;
      }

      &_long {
        grid-column: 1 / 2;
      }
    }
  }
}
